.selection-container__wrapper {
  width: 280px;
  padding-top: 41px;
  padding-bottom: 32px;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 630px;
    padding-top: 60px;
    padding-bottom: 72px;
  }

  @media (min-width: 1440px) {
    width: 1220px;
    padding-top: 69px;
    padding-bottom: 82px;
  }
}

.selection-container__inner-wrapper {
  position: relative;
}

.selection-container__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  margin-bottom: 37px;
  margin-left: -1px;

  @media (min-width: 768px) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 54px;
  }

  @media (min-width: 1440px) {
    margin-bottom: 56px;
  }
}

.selection-container__title::before {
  @media (max-width: 767px) {
    position: absolute;
    content: "";
    width: 320px;
    top: -28px;
    left: -20px;
    border-top: 1px solid #cdcdcd;
  }
}

.selection-container__text {
  width: 226px;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;

  @media (min-width: 768px) {
    width: 323px;
    font-size: 20px;
    line-height: 30px;
  }
}

.selection-container__button {
  margin-bottom: 21px;
  font-size: 16px;
  line-height: 20px;
  min-width: 280px;
  padding-top: 9px;
  padding-bottom: 10px;

  @media (min-width: 768px) {
    margin-bottom: 0;
    min-width: 282px;
    font-size: 20px;
    line-height: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-left: 2px;
  }

  @media (min-width: 1440px) {
    min-width: 568px;
  }
}
